import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasGrupoEmpresaConfig } from '../../../../dependencies/grupoEmpresaConfigDependency';
import { CLASS_NAME, CodigoTributacaoServico } from '../../../../models/dtos/ger/codigoTributacaoServico';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface CodigoTributacaoServicoEditViewProps extends EditViewProps<CodigoTributacaoServico> {}

const CodigoTributacaoServicoEditView: FC<CodigoTributacaoServicoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<CodigoTributacaoServico>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent
      title="Manutenção de Código de Tributação de Serviços"
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasGrupoEmpresaConfig(globalReducer.globalParameter)]}
    >
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 9, required: true } }}
              id="codigoTributacao"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Código"
              placeholder="Informe o Código da Tributação."
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 10 } }}
              id="codigoListaServico"
              isPrependLabel={true}
              label="Código da Lista"
              placeholder="Informe o Código da Lista."
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 100 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              placeholder="Informe a Descrição da Tributação."
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CodigoTributacaoServicoEditView;
